/* eslint-disable array-callback-return */
import csv from "csv";
import * as XLSX from "xlsx";
import {
  STOK_TAKE_UPLOAD_JSON,
  VALIDATE_PO_SKU_INFO_ID,
  STATUS,
} from "apiServices/action";

export const templateDownload = () => {
  document.getElementById("download-template-xls").click();
};
export const csvReader = (e, fieldArray, setIsProgressPercent) => {
  return new Promise((resolve, reject) => {
    let i = 0;
    let jsonArray = [];
    const reader = new FileReader();
    reader.onload = () => {
      csv.parse(reader.result, (err, data) => {
        if (data.length > 2001) {
          //why 2001? , because line 1 is header
          setIsProgressPercent(0);
          resolve(false);
        }
        data.map((obj, index) => {
          if (index === 0) {
            i++;
            return false; // skip
          }

          let newObject = {};
          newObject["id"] = index;

          let csvArray = data[i].toString().split(";");
          csvArray.map((csvObj, csvIndex) => {
            newObject[fieldArray[csvIndex + 1]] = csvObj;
          });
          i++;
          jsonArray.push(newObject);

          //count percent
          let percentage = ((index + 1) * 25) / data.length;
          setIsProgressPercent(Math.ceil(percentage));
        });
      });
    };

    //checking
    setTimeout(() => {
      resolve(jsonArray);
    }, 200);
    reader.readAsBinaryString(e[0]);
  });
};

export const xlsReader = async (
  e,
  fieldArray,
  setIsProgressPercent,
  setIsJsonArray,
  dispatch
) => {
  let i = 0;
  let jsonArray = [];
  const reader = new FileReader();
  let data = null;
  let error = false;

  return new Promise((resolve, reject) => {
    var fr = new FileReader();
    reader.onload = async (evt) => {
      /* Parse data */
      const bstr = evt.target.result;
      const wb = XLSX.read(bstr, { type: "binary" });
      /* Get first worksheet */
      const wsname = wb.SheetNames[0];
      const ws = wb.Sheets[wsname];
      /* Convert array of arrays */
      data = XLSX.utils.sheet_to_csv(ws, { header: 1 });
      /* Update state */

      await csv.parse(data, (err, data) => {
        if (data.length > 2001) {
          //why 2001? , because line 1 is header
          setIsProgressPercent(0);
          resolve(false);
        }
        data.map((obj, index) => {
          let newObject = {};
          newObject["id"] = index;

          let csvArray = data[i].toString().split(",");
          csvArray.map((csvObj, csvIndex) => {
            newObject[fieldArray[csvIndex + 1]] = csvObj;
          });
          i++;
          jsonArray.push(newObject);

          //count percent
          let percentage = ((index + 1) * 25) / data.length;
          setIsProgressPercent(Math.ceil(percentage));
        });
      });

      //checking
      setTimeout(() => {
        resolve(jsonArray);
      }, 200);
    };

    reader.readAsBinaryString(e[0]);
  });
};

export const putCsvFieldJson = ({ dispatch, jsonArray }) => {
  dispatch({ type: STOK_TAKE_UPLOAD_JSON, csvField: jsonArray });
};

export const validateColumn = async ({
  field,
  jsonArray,
  setIsProgressPercent,
  fileName,
  dispatch,
  warehouseArea,
  warehouseUuid,
  outboundType,
  setIsCheckingSchema,
  warehousesData,
}) => {
  //warehouse
  let warehouseCodeList = [];
  let warehouseUuidList = [];
  let warehouseNameList = [];
  if (warehousesData) {
    warehousesData.data.map((d) => {
      warehouseCodeList.push(d.code);
      warehouseUuidList.push(d.id);
      warehouseNameList.push(d.name);
    });
  }

  //warehouse area
  let wh_area_label = !warehouseArea
    ? []
    : warehouseArea.map((data) => data.label.toUpperCase());
  let wh_area_value = !warehouseArea
    ? []
    : warehouseArea.map((data) => data.value);
  let wh_outboundtype_label = !outboundType
    ? []
    : outboundType.map((data) => data.label.toUpperCase());
  let wh_outboundtype_value = !outboundType
    ? []
    : outboundType.map((data) => data.value);
  let csv_schema = !field
    ? []
    : field.map((data) => data.text.toUpperCase().replace(/\s/g, ""));
  let distinctPOSKUInfoId = [];
  let distinctSKUNumber = [];

  // check schema
  let checkSchema = true;
  let i = 0;
  const schemaRow = jsonArray[0];
  for (var key in schemaRow) {
    if (key == "id") {
      continue;
    }
    let keys = schemaRow[key].toUpperCase().replace(/\s/g, "");
    if (
      key !== "undefined" &&
      (csv_schema[i + 1] !== keys || !csv_schema.includes(keys))
    ) {
      console.clear();
      console.log("--Schema Not Same--");
      console.log("Schema \t\t: ", csv_schema[i + 1]);
      console.log("File SChema : ", keys);
      checkSchema = false;
      setIsCheckingSchema(false);
      return 0;
    }
    i++;
  }

  jsonArray.shift();
  await jsonArray.map((data, index) => {
    let emptyRow = true; // if empty row dont show
    field.map((fieldData, fieldIndex) => {
      //if null then return
      if (fieldData === undefined || fieldData === null) {
        return;
      }

      if (fieldData.dataField !== "id" && data[fieldData.dataField] == "") {
        return;
      } else if (fieldData.dataField == "id") {
      } else {
        emptyRow = false;
      }
      // end checking empty

      //set warehouse id
      data["warehouseSource"] = warehouseUuid;

      //checking warehouse
      if (warehouseCodeList && fieldData.dataField == "warehouse") {
        if (!warehouseCodeList.includes(data[fieldData.dataField])) {
          data[fieldData.dataField + "Status"] = false;
          data[fieldData.dataField + "Reason"] = `Warehouse Not Found`;
          data["warehouseUuid"] = "";
          data["warehouseName"] = "";
          return;
        } else {
          let i = warehouseCodeList.indexOf(data[fieldData.dataField]);
          data["warehouseUuid"] = warehouseUuidList[i];
          data["warehouseName"] = warehouseNameList[i];
        }
      }

      //checing warehouse Destination
      if (warehouseCodeList && fieldData.dataField == "warehouseDestination") {
        if (!warehouseCodeList.includes(data[fieldData.dataField])) {
          data[fieldData.dataField + "Status"] = false;
          data[fieldData.dataField + "Reason"] = `Warehouse Not Found`;
          data["warehouseDestinationUuid"] = "";
          data["warehouseDestinationName"] = "";
          return;
        } else {
          let i = warehouseCodeList.indexOf(data[fieldData.dataField]);
          data["warehouseDestinationUuid"] = warehouseUuidList[i];
          data["warehouseDestinationName"] = warehouseNameList[i];
        }
      }

      //checking warehouse area
      if (fieldData.dataField == "areaName") {
        if (!wh_area_label.includes(data[fieldData.dataField].toUpperCase())) {
          data[fieldData.dataField + "Status"] = false;
          data[fieldData.dataField + "Reason"] = `Warehouse Area Not Found`;
          data["areaSource"] = null;
          return;
        } else {
          let i = wh_area_label.indexOf(
            data[fieldData.dataField].toUpperCase()
          );
          data["areaSource"] = wh_area_value[i];
        }
      }

      if (fieldData.dataField == "areaNameDestination") {
        if (!wh_area_label.includes(data[fieldData.dataField].toUpperCase())) {
          data[fieldData.dataField + "Status"] = false;
          data[fieldData.dataField + "Reason"] = `Warehouse Area Not Found`;
          data["areaDestination"] = null;
          return;
        } else {
          let i = wh_area_label.indexOf(
            data[fieldData.dataField].toUpperCase()
          );
          data["areaDestination"] = wh_area_value[i];
        }
      }

      //checking outbound type
      if (
        wh_outboundtype_value.length > 0 &&
        fieldData.dataField == "outboundType"
      ) {
        if (
          !wh_outboundtype_label.includes(
            data[fieldData.dataField].toUpperCase()
          )
        ) {
          data[fieldData.dataField + "Status"] = false;
          data[fieldData.dataField + "Reason"] = `Outbound Type Not Found`;
          data["outboundTypeCode"] = null;
          return;
        } else {
          let i = wh_outboundtype_label.indexOf(
            data[fieldData.dataField].toUpperCase()
          );
          data["outboundTypeCode"] = wh_outboundtype_value[i];
        }
      }

      //newgrade
      if (fieldData.dataField == "newGrade") {
        let arr_grade = ["WASTE", "SEMI WASTE", ""];
        if (
          data[fieldData.dataField] &&
          !arr_grade.includes(data[fieldData.dataField].toUpperCase())
        ) {
          //if not waste or semi waste
          if (parseInt(data[fieldData.dataField])) {
            //if number, check sku number
            if (!distinctSKUNumber.includes(data[fieldData.dataField])) {
              distinctSKUNumber.push(data[fieldData.dataField]);
            }
            data[fieldData.dataField + "Status"] = true;
          } else if (data[fieldData.dataField] == "") {
            //if null
            data[fieldData.dataField + "Status"] = true;
          } else {
            //if not number => undefined
            data[fieldData.dataField + "Status"] = false;
            data[fieldData.dataField + "Reason"] = `Grade Undefined`;
          }
        } else {
          data["newGradeUuid"] = data[fieldData.dataField];
        }
      }

      //set ripeness
      let ripeness = null;
      if (
        (fieldData.dataField == "ripenessLevel" ||
          fieldData.dataField == "newRipeness") &&
        data[fieldData.dataField] !== undefined
      ) {
        let ripeness_ = data[fieldData.dataField].split(" ");
        ripeness = ripeness_[1];
      }

      //checking type
      let tmp_data = data[fieldData.dataField];
      let stat = null;
      if (fieldData.type == "number") {
        if (isNaN(tmp_data)) {
          stat = false;
        } else {
          stat = true;

          if (fieldData.dataField == "wmsPoSkuCode") {
            if (!distinctPOSKUInfoId.includes(tmp_data)) {
              distinctPOSKUInfoId.push(tmp_data);
            }
          }

          //distinct sku angka maka push ke array buat di cek
          if (fieldData.dataField == "skuNumber") {
            if (!distinctSKUNumber.includes(tmp_data) && tmp_data !== "") {
              distinctSKUNumber.push(tmp_data);
            }
          }
        }
      } else if (fieldData.type == "date_indo") {
        //checking date
        let regex_date = /(0[1-9]|[12][0-9]|3[01])[-.](0[1-9]|1[012])[-.](19|20)\d\d/;
        if (tmp_data.match(regex_date)) {
          stat = true;
        } else {
          data[fieldData.dataField + "Status"] = false;
          data[
            fieldData.dataField + "Reason"
          ] = `Date format must be 'dd-mm-yyyy'`;
          return;
        }
        // console.log('stat date', stat)
        // console.log(`${fieldData.dataField}Reason`, data[fieldData.dataField + "Reason"])
      } else {
        stat = true;
      }

      //grade can null
      let canNull = [
        "grade",
        "outboundReason",
        "ripenessLevel",
        "newRipeness",
        "newGrade",
      ];
      if (canNull.includes(fieldData.dataField) && tmp_data == "") {
        stat = true;
      }

      //check newGrade, jika angka maka cek ke distinct sku
      if (fieldData.dataField == "newGrade") {
        let arr_grade = ["WASTE", "SEMI WASTE", ""];
        if (!arr_grade.includes(tmp_data.toUpperCase())) {
          //if not waste or semi waste
          if (parseInt(tmp_data)) {
            //if number
            stat = true;
            if (!distinctSKUNumber.includes(tmp_data) && tmp_data !== "") {
              distinctSKUNumber.push(tmp_data);
            }
          }
        }
      }

      data[fieldData.dataField + "Status"] = stat;
      if (!stat) {
        data[
          fieldData.dataField + "Reason"
        ] = `Invalid ${fieldData.text} \nShould be a ${fieldData.type}`;
      } else {
        data[fieldData.dataField + "Reason"] = "";
      }

      let percentage =
        ((index + 1 + parseInt(jsonArray.length)) * 50) /
        (parseInt(jsonArray.length) * 2);
      setIsProgressPercent(Math.ceil(percentage));
    });
    data["emptyRow"] = emptyRow;
  });

  //end checking type
  dispatch({ type: STOK_TAKE_UPLOAD_JSON, csvData: jsonArray });
  dispatch({ type: STOK_TAKE_UPLOAD_JSON, csvFileName: fileName });
  dispatch({
    type: STOK_TAKE_UPLOAD_JSON,
    csvDistinctPOSKUInfoId: distinctPOSKUInfoId,
  });
  dispatch({
    type: STOK_TAKE_UPLOAD_JSON,
    csvDistinctSKUNumber: distinctSKUNumber,
  });
};

export const cleaningData = async ({
  poSkuInfoValidated,
  poSkuNumberValidated,
  csvData,
  setIsProgressPercent,
  dispatch,
  status,
  transaction,
  setIsProgressStatus,
  nextPage,
}) => {
  //checking
  if (status === false || status === null) {
    return 0;
  }
  if (
    csvData === undefined ||
    csvData === null ||
    poSkuInfoValidated === null ||
    poSkuInfoValidated === undefined ||
    poSkuNumberValidated === null ||
    poSkuNumberValidated === undefined
  ) {
    console.clear();
    return 0;
  }

  //split data and id po sku code
  const wmsPoSkuCode = [];
  const wmsPoSkuUom = [];
  poSkuInfoValidated.map((data) => {
    wmsPoSkuCode.push(data.wmsPoSkuCode);
    wmsPoSkuUom.push(data.uom);
  });

  //split sku number and skuUUid sku number
  let skuNumberArray = [];
  let skuUuidArray = [];
  let skuPhotosArray = [];
  let skuUomArray = [];
  poSkuNumberValidated.map((data) => {
    skuNumberArray.push(data.skuNumber);
    skuUuidArray.push(data.id);
    skuPhotosArray.push(data.imageMd);
    skuUomArray.push(data.uom);
  });

  //cleaning data
  let cleanData = [];
  csvData.map((data, index) => {
    //if empty row then skip
    if (data.emptyRow === true) {
      return 0;
    }

    //format fixing
    data.areaSource = parseInt(data.areaSource);
    data.areaDestination = parseInt(data.areaDestination);

    //if transaction stocktake then source and destination is same
    if (
      transaction == "stock_take" ||
      transaction == "repacking" ||
      transaction == "outbound"
    ) {
      data.areaDestination = parseInt(data.areaSource);
      data.warehouseDestination = parseInt(data.warehouseSource);
    }
    //get index dari poskucode
    let indexId = wmsPoSkuCode.indexOf(data.wmsPoSkuCode);

    //get validated data
    let skuValidatedData = poSkuInfoValidated[indexId];

    if (
      !skuValidatedData ||
      skuValidatedData.isExist === undefined ||
      skuValidatedData.isExist === false ||
      skuValidatedData.isExist === null
    ) {
      data.wmsPoSkuCodeStatus = false;
      data.wmsPoSkuCodeReason = "PO SKU Code Not Found";
    } else {
      // skuValidatedData.expiryDate = skuValidatedData.expiryDate
      data.inboundDate = skuValidatedData.inboundDate;
      data.uom = wmsPoSkuUom[indexId];
    }

    //check grade
    let tmp_grade = data.newGrade;
    let arr_grade = ["WASTE", "SEMI WASTE", ""];
    if (
      tmp_grade !== undefined &&
      !arr_grade.includes(tmp_grade.toUpperCase())
    ) {
      //if not waste or semi waste
      if (skuNumberArray.includes(tmp_grade)) {
        let idx = skuNumberArray.indexOf(tmp_grade);
        data.newGradeStatus = true;
        data.newGradeUuid = skuUuidArray[idx];
      } else if (tmp_grade == "") {
        data.newGradeUuid = "";
        data.newGradeStatus = true;
      } else {
        //if not number => undefined
        data.newGradeReason = "Grade Undefined";
        data.newGradeStatus = false;
      }
    }

    //check Sku Number
    let tmp_sku_number = data.skuNumber;
    if (!skuNumberArray.includes(tmp_sku_number)) {
      data.skuNumberStatus = false;
      data.skuNumberReason = "SKU Number Not Found";
      data.skuUuid = "";
    } else {
      let idx = skuNumberArray.indexOf(tmp_sku_number);
      data.skuUuid = skuUuidArray[idx];
      data.photo = skuPhotosArray[idx];
    }

    //check New Grade
    let tmp_new_grade = data.newGrade;
    console.log("tmp_new_grade", tmp_new_grade);
    if (
      (tmp_new_grade && arr_grade.includes(tmp_new_grade.toUpperCase())) ||
      tmp_new_grade == ""
    ) {
      data.newGradeReason = "";
      data.newGradeStatus = true;
      data.newGradeUuid = null;
      data.newGradeUom = null;
    } else {
      if (skuNumberArray.includes(tmp_new_grade)) {
        let idx = skuNumberArray.indexOf(tmp_new_grade);
        data.newGradeReason = "";
        data.newGradeStatus = true;
        data.newGradeUuid = skuUuidArray[idx];
        data.newGradeUom = skuUomArray[idx];
      } else {
        data.newGradeReason = "SKU Number Not Found";
        data.newGradeStatus = false;
        data.newGradeUuid = null;
        data.newGradeUom = null;
      }
    }

    //merging_array
    cleanData.push({ ...data, ...skuValidatedData });
  });
  dispatch({ type: STOK_TAKE_UPLOAD_JSON, csvData: cleanData });
  setIsProgressPercent(Math.ceil(100));
  setTimeout(() => {
    nextPage();
  }, 200);
};

export const resetCsvResist = async ({
  dispatch,
  resetStatus,
  setIsResetCsvPersist,
}) => {
  dispatch({ type: STOK_TAKE_UPLOAD_JSON, csvData: null });
  dispatch({ type: STOK_TAKE_UPLOAD_JSON, csvFileName: null });
  dispatch({
    type: STOK_TAKE_UPLOAD_JSON,
    csvDistinctPOSKUInfoId: null,
  });
  dispatch({ type: VALIDATE_PO_SKU_INFO_ID, skuCodeValidated: null });
  dispatch({ type: VALIDATE_PO_SKU_INFO_ID, skuCodeValidated: null });
  dispatch({ type: STATUS, status: null });
  setIsResetCsvPersist(true);
};
